.Gender-Based {
  width: 60%;
  padding: 0 1em;
  height: 250px;
  overflow-x: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.Gender-Based::-webkit-scrollbar {
  display: none;
}

.Gender-Based ul {
  display: flex;
  justify-content: space-between;
  height: 55px;
  padding: 1em 0;
  /* border-top: 1px solid #f2f6f3;  */
}

.Gender-Based ul:first-of-type {
  color: #7fbcbe;
  font-weight: 600;
}

.Gender-Based ul li {
  width: 18%;
}

.Gender-Based ul li:nth-child(1),
.Gender-Based ul li:nth-child(5) {
  width: 20%;
}

.Gender-Based ul:last-of-type {
  color: #f16161;
}

@media (max-width: 50em) {
  .Gender-Based {
    width: 100%;
    margin-bottom: 2em;
    overflow: -moz-scrollbars-intial;
    -ms-overflow-style: initial;
    scrollbar-width: inherit;
  }

  .Gender-Based::-webkit-scrollbar {
    display: initial;
  }
}

@media (max-width: 30em) {
  .Gender-Based {
    height: auto;
  }

  .Gender-Based .table {
    width: 600px;
    height: 320px;
    padding-bottom: 1em;
  }
}
