.Dashboard {
  width: 100%;
  padding: 4em;
}

.Dashboard .overrall ul {
  display: flex;
  justify-content: space-between;
}

.Dashboard .overrall ul li {
  display: flex;
  flex-wrap: wrap;
  border-left: 1px solid #f2f6f3;
  padding: 1em 2em;
}

.Dashboard .overrall ul li img {
  height: 20px;
  margin: auto 0;
  margin-right: 0.5em;
}

.Dashboard .overrall ul li span {
  font-weight: 600;

  display: inline-block;
  margin: auto 0;
}

.Dashboard .overrall ul li span:first-of-type {
  display: flex;
  width: 20px;
  height: 20px;
  margin-right: 0.5em;
  background-color: #7fbcbe;
  border-radius: 5px;
}

.Dashboard .overrall ul li span:first-of-type img {
  width: auto;
  height: 12px;
  margin: auto;
}

.Dashboard .overrall ul li span:last-of-type {
  font-size: 0.875rem;
  font-weight: normal;
  color: #b1b5b2;
  display: block;
  width: 100%;
  margin-top: 0.8em;
}

.Dashboard .provinces {
  display: flex;
}

@media (max-width: 50em) {
  .Dashboard .overrall ul {
    flex-wrap: wrap;
  }

  .Dashboard .overrall ul li {
    width: 50%;
    border-bottom: 1px solid #f2f6f3;
    border-left: none;
  }

  .Dashboard .provinces {
    flex-direction: column;
  }
}

@media (max-width: 50em) {
  .Dashboard {
    padding: 1em;
    overflow: hidden;
  }
}
