.Overrall-Province {
  display: flex;
  margin: 2em 0 0;
}

.Overrall-Province .province-number {
  width: 60%;
  margin: auto 0;
  margin-right: 1em;
}

.Overrall-Province .province-number ul {
  display: flex;
  justify-content: space-between;
}

.Overrall-Province .province-number ul:nth-child(1) li {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 65px;
  height: 65px;
  /* overflow: hidden; */
}

.Overrall-Province .province-number ul:nth-child(1) li > span {
  /* position: absolute; */
  display: flex;
  width: 65px;
  height: 65px;
  margin: 0 auto;
  /* margin-left: 0.5em; */
  background-color: #999;
  border-radius: 50%;
}

.Overrall-Province .province-number ul:nth-child(1) li > span span {
  display: block;
  margin: auto;
}
/* .Overrall-Province .province-number ul:nth-child(1) li:nth-child(2) span {
  width: 50px;
  height: 50px;
} */

.Overrall-Province .province-number ul:nth-child(2) {
  margin: 0 2em 1em;
  border-bottom: 1px solid #d7dbd8;
}

.Overrall-Province .province-number ul:nth-child(2) li {
  height: 30px;
  border-left: 1px solid #d7dbd8;
}

.Overrall-Province .province-number ul:nth-child(2) li:nth-child(even) {
  height: 15px;
  margin-top: 15px;
}

.Overrall-Province .province-number ul:nth-child(3) {
  /* font-size: 0.875rem; */
  font-weight: 600;
  color: #b1b5b2;
  margin: 0 1.1em;
}

.Overrall-Province .province-number ul:nth-child(3) li {
  width: 30px;
  height: 30px;
}

.Overrall-Province .covid-statistics {
  display: flex;
  width: 40%;
  height: 200px;
  padding: 1em 0;
  background-color: #7fbcbe;
}

.Overrall-Province .covid-statistics div {
  padding: 1em;
}

.Overrall-Province .covid-statistics div:first-of-type {
  width: 40%;
  text-align: center;
}

.Overrall-Province .covid-statistics div:first-of-type figure span {
  font-weight: 600;
  position: absolute;
  margin-top: -70px;
  margin-left: -18px;
}

.Overrall-Province .covid-statistics div:first-of-type img {
  display: flex;
  width: 50%;
  height: 100px;
  margin: auto;
}

.Overrall-Province .covid-statistics div:first-of-type h4 {
  font-size: 0.875rem;
  width: 70px;
  margin: 1em auto;
  padding: 0.4em 1em;
  background-color: #fff;
  border-radius: 5px;
}

.Overrall-Province .covid-statistics div:last-of-type {
  color: #fff;
  width: 60%;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
}

.Overrall-Province .covid-statistics div:last-of-type h2 {
  font-size: 1rem;
  margin-bottom: 1.5em;
}

.Overrall-Province .covid-statistics div:last-of-type ul li {
  display: flex;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
  margin-bottom: 0.8em;
}

.Overrall-Province .covid-statistics div:last-of-type ul li span:first-of-type {
  width: 40px;
}

@media (max-width: 50em) {
  .Overrall-Province {
    flex-direction: column;
    border-bottom: 1px solid #f2f6f3;
    margin-bottom: 2em;
    padding-bottom: 2em;
  }

  .Overrall-Province .province-number {
    width: 100%;
    margin-bottom: 1em;
  }

  .Overrall-Province .covid-statistics {
    width: 100%;
  }
}

@media (max-width: 30em) {
  .Overrall-Province .province-number {
    overflow: scroll;
  }

  .Overrall-Province .province-number div {
    width: 1000px;
  }

  .Overrall-Province .covid-statistics div:first-of-type img {
    width: 100%;
    height: auto;
    margin: auto;
  }

  .Overrall-Province .covid-statistics div:first-of-type figure span {
    margin-left: -20px;
  }
}
