.Province-Graph {
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  height: 250px;
  overflow-x: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.Province-Graph::-webkit-scrollbar {
  display: none;
}

.Province-Graph > div {
  width: 50%;
  padding: 1em;
  display: flex;
  flex-wrap: wrap;
  border-left: 1px solid #f2f6f3;
}

.Province-Graph > div h3 {
  width: 100%;
}

.Province-Graph .donutchart {
  width: 60%;
}

.Province-Graph > div ul {
  font-size: 0.675rem;
  margin-top: 3em;
}

.Province-Graph > div ul li {
  margin-bottom: 0.5em;
}

.Province-Graph > div ul span {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin: auto 0;
  margin-right: 0.5em;
  background-color: #000;
  border-radius: 50%;
}

@media (max-width: 50em) {
  .Province-Graph {
    width: 100%;
    height: 420px;
    overflow: -moz-scrollbars-intial;
    -ms-overflow-style: initial;
    scrollbar-width: inherit;
  }

  .Province-Graph > div {
    border-left: none;
    border-top: 1px solid #f2f6f3;
  }
}

@media (max-width: 30em) {
  .Province-Graph {
    height: 600px;
  }

  .Province-Graph .donutchart {
    width: 100%;
  }
}
